export const Subscribe = ()=>{
  if(ActiveUser){
    var _CollectionFB = `/hhh/${ActiveUser}/params/`;  
    db.collection(_CollectionFB).onSnapshot((querySnapshot) => {    
        querySnapshot.forEach((doc) => {
            var s = doc.data();
            if(!s.isActive){
              cleanTokenandLogout()
            } else if( s.isActive && !s.isActive.active){
              cleanTokenandLogout()
            }else{
              _Dispatch(UpdKeyValue({key:'user',value:s}));
              let _fbtoken = window.localStorage.getItem('fbtkClnt');
              if(_fbtoken && s.frBsToken!==_fbtoken){
                //Update({frBsToken:_fbtoken});
              }
            }
        });
    });
  }
}




