export function LoadData() {  
  return function (dispatch, getState) {
    if(!_Dispatch){
      _Dispatch = dispatch;
    }
    if(!_State){
      _State = getState;
    }
    


    var doc = {id:'dgt047j3',limit:1000,page:1,sortBy:'date.desc'};
    const query= ` 
    query($doc: FindbyIdCda!){
      payload:  getCdasbyId(cda: $doc) {
        id,
        isAdmin        
      }
    }  
    `;
    let variables={doc};    
    _Util.fetchGraphQL(GRAPHQLURL,{query,variables})
    .then(res => {
     
      if(res && res.data && res.data.payload){ 
        ActiveUser = res.data.payload.id;
        if(res.data.payload.isAdmin){
          _Util.changetheme();
          dispatch(UpdKeyValue({key:'isAdmin',value:true}));
          dispatch(LoadUsersActive())
        }else{
          Subscribe(); 
          dispatch(setAuthenticate(true));
          dispatch(appLoaded(true));
          dispatch(_formOberves());
        }
        //dispatch(LoadServiceAreas());
      }else{
        dispatch(appLoaded(true));        
      }      
    }).catch(error => {      
      console.log(error); //eslint-disable-line
    });
  }
}



