function filtersEarnings(payload) {
   var filters = {}
    payload.map(t=>{
      var _month = (new Date(t.date)).getMonth()+1;
      var _year = (new Date(t.date)).getFullYear();
      var _dayW = (new Date(t.date)).getDay()+1;
      var _day = (new Date(t.date)).getDate();
      var _week = _Util.getWeek(t.date);
      if(_month===10){
        //console.log(_day)
      }
      if(!filters[_year]){
        filters[_year]={}
        filters[_year]['months']={};
        filters[_year]['daysWeek']={};
        filters[_year]['Weeks']={};
        filters[_year]['list']=[];
        filters[_year]['total']=0;
        filters[_year]['amount']=0;
        filters[_year]['tip']=0;
      }
      var _tt = t.amount + t.tip;
      var _t = t;
      _t['total']=_tt;
      _t['dayw']=_dayW;
      _t['day']=_day;
      filters[_year]['list'].push(_t);      
      filters[_year]['total']+=_tt;
      filters[_year]['amount']+=t.amount;
      filters[_year]['tip']+=t.tip; 


      if(!filters[_year]['months'][_month]){
        filters[_year]['months'][_month]={}
        filters[_year]['months'][_month]['list']=[]
        filters[_year]['months'][_month]['total']=0;
        filters[_year]['months'][_month]['amount']=0
        filters[_year]['months'][_month]['tip']=0
      }
      
      filters[_year]['months'][_month]['list'].push(_t);  
      filters[_year]['months'][_month]['total']+=_tt;
      filters[_year]['months'][_month]['amount']+=t.amount;
      filters[_year]['months'][_month]['tip']+=t.tip; 
      
      if(!filters[_year]['daysWeek'][_dayW]){
        filters[_year]['daysWeek'][_dayW]={}
        filters[_year]['daysWeek'][_dayW]['list']={};
        filters[_year]['daysWeek'][_dayW]['total']=0;
        filters[_year]['daysWeek'][_dayW]['amount']=0;
        filters[_year]['daysWeek'][_dayW]['tip']=0;
        filters[_year]['daysWeek'][_dayW]['hrs']=0;
      }
      var _w = _t;
      _w['week']= _week;

      if(!filters[_year]['daysWeek'][_dayW]['list'][_week]){
        filters[_year]['daysWeek'][_dayW]['list'][_week]={}
        filters[_year]['daysWeek'][_dayW]['list'][_week]['total']=0;
        filters[_year]['daysWeek'][_dayW]['list'][_week]['amount']=0;
        filters[_year]['daysWeek'][_dayW]['list'][_week]['tip']=0;
        filters[_year]['daysWeek'][_dayW]['list'][_week]['hrs']=0;
      }

      filters[_year]['daysWeek'][_dayW]['total']+=_tt;
      filters[_year]['daysWeek'][_dayW]['amount']+=_t.amount;
      filters[_year]['daysWeek'][_dayW]['tip']+=_t.tip; 
      filters[_year]['daysWeek'][_dayW]['hrs']+=_t.hrs;

      filters[_year]['daysWeek'][_dayW]['list'][_week]['total']+=_tt;
      filters[_year]['daysWeek'][_dayW]['list'][_week]['amount']+=_t.amount;
      filters[_year]['daysWeek'][_dayW]['list'][_week]['tip']+=_t.tip;
      filters[_year]['daysWeek'][_dayW]['list'][_week]['hrs']+=_t.hrs;
      filters[_year]['daysWeek'][_dayW]['list'][_week]['day']=_t.day;
      filters[_year]['daysWeek'][_dayW]['list'][_week]['month']=_month;
      





      if(!filters[_year]['Weeks'][_week]){
        filters[_year]['Weeks'][_week]= {}
        filters[_year]['Weeks'][_week]['list'] = {}
        filters[_year]['Weeks'][_week]['total']=0;
        filters[_year]['Weeks'][_week]['amount']=0;
        filters[_year]['Weeks'][_week]['tip']=0;
        filters[_year]['Weeks'][_week]['hrs']=0;
      } 
      filters[_year]['Weeks'][_week]['total']+=_tt;
      filters[_year]['Weeks'][_week]['amount']+=_t.amount;
      filters[_year]['Weeks'][_week]['tip']+=_t.tip; 
      filters[_year]['Weeks'][_week]['hrs']+=_t.hrs;


      if(!filters[_year]['Weeks'][_week]['list'][_dayW]){
        filters[_year]['Weeks'][_week]['list'][_dayW]={}
        filters[_year]['Weeks'][_week]['list'][_dayW]['total']=0;
        filters[_year]['Weeks'][_week]['list'][_dayW]['amount']=0;
        filters[_year]['Weeks'][_week]['list'][_dayW]['tip']=0;
        filters[_year]['Weeks'][_week]['list'][_dayW]['hrs']=0;
      }
      

      filters[_year]['Weeks'][_week]['list'][_dayW]['total']+=_tt;
      filters[_year]['Weeks'][_week]['list'][_dayW]['amount']+=_t.amount;
      filters[_year]['Weeks'][_week]['list'][_dayW]['tip']+=_t.tip;
      filters[_year]['Weeks'][_week]['list'][_dayW]['day']=_t.day;
      filters[_year]['Weeks'][_week]['list'][_dayW]['hrs']+=_t.hrs;
      filters[_year]['Weeks'][_week]['list'][_dayW]['month']=_month;
      
    })
    
    //console.log(filters)
    _Dispatch(UpdKeyValue({key:'earningsFilters',value:filters}));   


}

export function LoadUserServiceEarnings(email) {
  return function (dispatch, getState) {
    var doc = {email:email};
    const query= ` 
    query($doc: FindSchedule!){
      payload:  earningsList(cda: $doc) {
        amount,
        tip,
        hrs,
        date
      }
    }  
    `;
    let variables={doc};
    _Util.fetchGraphQL(GRAPHQLURL,{query,variables}) 
    .then(res => {
      if(res && res.data && res.data.payload){
        dispatch(UpdKeyValue({key:'earningsList',value:res.data.payload}));
        filtersEarnings(res.data.payload);             
      }
    }).catch(error => {      
      console.log(error);
    });
  }
}






export function LoadUserServiceInfo(email) {
  return function (dispatch, getState) {
    var doc = {email:email};
    const query= ` 
    query($doc: FindSchedule!){
      payload:  infoByUser(cda: $doc) {
        depositList{
          amount,
          date
        },
        scheduleList {
          pay,
          hrs,
          startTime,
          location,
        },
        earningsList{
          amount,
          tip,
          hrs,
          date
        }
      }
    }  
    `;
    let variables={doc};
    _Util.fetchGraphQL(GRAPHQLURL,{query,variables}) 
    .then(res => {
      if(res && res.data && res.data.payload){
        var f = res.data.payload;
        if(f["depositList"]){
          dispatch(UpdKeyValue({key:'depositList',value:f["depositList"]})); 
        }
        if(f["earningsList"]){
          dispatch(UpdKeyValue({key:'earningsList',value:f["earningsList"]})); 
          filtersEarnings(f["earningsList"]);     
        }
        if(f["scheduleList"]){
          dispatch(UpdKeyValue({key:'scheduleList',value:f["scheduleList"]})); 
        }
      }
    }).catch(error => {      
      console.log(error);
    });
  }
}




